import { colorService, TManufacturer } from './colors.service';
import React, { ChangeEvent } from 'react';

const EMPTY_MANUFACTURER = ' ';

export function ColorSearch() {
  const handleSearchNumberChange = (e: ChangeEvent<HTMLInputElement>): void => {
    colorService.updateSearchNumber(e.target.value);
  };

  const handleManufacturerSelect = (e: ChangeEvent<HTMLSelectElement>): void => {
    const value = e.target.value === EMPTY_MANUFACTURER ? undefined : e.target.value as TManufacturer;
    colorService.updateManufacturer(value);
  };

  const handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
    const hexColor = e.target.value;
    let searchColor: string = '';

    if (hexColor.startsWith('#') && hexColor.length === 7) {
      searchColor = hexColor;
    } else if (hexColor.length === 6) {
      searchColor = `#${hexColor}`;
    }

    colorService.setSearchColor(searchColor);
  };


  const options: Array<TManufacturer> = ['DMC', 'Gamma', 'Anchor', 'Madeira'];

  return (
    <div className="manufacturer-filter__wrapper">
      <div className="control">
        <div>
          
        </div>
        <input type="text" onChange={handleSearchNumberChange}/>
      </div>

      <div className="control">
        <div></div>

        <select className="manufacturer-filter__select" onChange={handleManufacturerSelect}>
          <option value={undefined}>{EMPTY_MANUFACTURER}</option>
          {options.map(item => (
            <option key={item} value={item}>{item}</option>
          ))}
        </select>
      </div>

      <div className="control">
        <div>
          HEX
        </div>
        <input type="text" onChange={handleChange}/>
      </div>

    </div>
  );
}
