import React from 'react';
import { colorService } from './colors.service';
import './styles.css';
import { useRxBind } from '../utils/use-rx-bind';
import { ColorSearch } from './color-search.component';
import { IColorModel } from './color.const';

const stringToHex = (color: number): string => {
  const result = color.toString(16);
  return result.length === 1 ? `0${result}` : result;
};

const rgbToHex = (r: number, g: number, b: number) => `#${stringToHex(r)}${stringToHex(g)}${stringToHex(b)}`;

const rawStringToHex = (str: string) => {
  const [r, g, b] = str.slice(4, str.length - 1).split(',');
  return rgbToHex(+r, +g, +b);
};

export function ColorsTable(): JSX.Element {
  const handleClick = (hexCode: string) => {
    return navigator.clipboard.writeText(hexCode);
  };

  const filteredColors = useRxBind(colorService.filteredColors$, []);

  return (
    <>
      <ColorSearch/>
      <div className="container">
        <div className="container__table">
          <table>
            <thead>
            <tr>
              <td>DMC</td>
              <td>Gamma</td>
              <td>Anchor</td>
              <td>Madeira</td>
              <td></td>
              <td></td>
              <td style={{ paddingLeft: 20 }}>HEX</td>
            </tr>
            </thead>
            <tbody>
            {filteredColors.map(((row: IColorModel, index: number) => {
              return (
                <tr key={index}>
                  <td>{row.DMC}</td>
                  <td>{row.Gamma}</td>
                  <td>{row.Anchor}</td>
                  <td>{row.Madeira}</td>
                  <td>{row.name}</td>
                  <td style={{ backgroundColor: row.color }}/>
                  <td style={{ paddingLeft: 20 }}>
                    <button
                      className="button"
                      onClick={() => handleClick(rawStringToHex(row.color))}
                      style={{
                        padding: 10,
                        background: 'none',
                        fontSize: 18,
                        border: 'none',
                        textTransform: 'uppercase'
                      }}
                    >
                      {rawStringToHex(row.color)}
                    </button>
                  </td>
                </tr>
              );
            }))}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );

}

