import { useCallback, useRef, useSyncExternalStore } from 'react';
import { BehaviorSubject, Observable } from 'rxjs';

const initialValueSymbol = Symbol('rx bind initial value');

/**
 *         
 */
export function useRxBind<T>(stream: BehaviorSubject<T>): T;
export function useRxBind<T>(stream: Observable<T>): T | void;
export function useRxBind<T>(stream: Observable<T>, defaultValue: T): T;
export function useRxBind<T>(stream: Observable<T>, defaultValue?: T): T | void {
  const cache = useRef<typeof initialValueSymbol | T | undefined>(initialValueSymbol);

  const subscribe = useCallback(
    (forceRerender: VoidFunction) => {
      const subscription = stream.subscribe((nextState: T) => {
        const isSameState = cache.current === nextState;

        if (!isSameState) {
          cache.current = nextState;
          forceRerender();
        }
      });

      return () => subscription.unsubscribe();
    },
    [stream]
  );

  function getInitialValue(): T | undefined {
    if (defaultValue !== void 0) {
      return defaultValue;
    } else if (stream instanceof BehaviorSubject) {
      return stream.getValue() as T;
    } else {
      return void 0;
    }
  }

  const getSnapshot = useCallback((): T | undefined => {
    if (cache.current === initialValueSymbol) {
      cache.current = getInitialValue();
    }

    return cache.current;
  }, [stream]);

  return useSyncExternalStore(subscribe, getSnapshot);
}
