import nearestColor from './nearest-color';
import { BehaviorSubject, combineLatest, map, Observable } from 'rxjs';
import { ALL_COLORS, ALL_ROWS, IColorModel } from './color.const';

type TFilterByManufactureParams = {
  item: IColorModel;
  manufacturer: TManufacturer | undefined;
  searchNumber: string;
}

export type TManufacturer = keyof Pick<IColorModel, 'Anchor' | 'DMC' | 'Madeira' | 'Gamma'>;

function filterByManufacture(params: TFilterByManufactureParams): boolean {
  const { item, manufacturer, searchNumber } = params;

  return manufacturer
    ? item[manufacturer] === searchNumber
    : [item.DMC, item.Anchor, item.Gamma, item.Madeira].includes(searchNumber);
}

class ColorService {
  private static allRows: Array<IColorModel> = [];
  private searchColor$: BehaviorSubject<string | undefined> = new BehaviorSubject<string | undefined>(undefined);
  private manufacturer$: BehaviorSubject<TManufacturer | undefined> = new BehaviorSubject<TManufacturer | undefined>(undefined);
  private searchNumber$: BehaviorSubject<string | undefined> = new BehaviorSubject<string | undefined>(undefined);

  filteredColors$: Observable<Array<IColorModel>> = combineLatest([
    this.searchColor$,
    this.manufacturer$,
    this.searchNumber$
  ]).pipe(
    map(([searchColor, manufacturer, searchNumber]) => {
      if (searchNumber) {
        return ColorService.allRows
          .filter((item: IColorModel) => filterByManufacture({ item, searchNumber, manufacturer }));
      }

      if (!searchColor) return ColorService.allRows;

      return this.getFilteredColors(searchColor);
    })
  );

  updateManufacturer(value?: TManufacturer): void {
    this.manufacturer$.next(value);
  }

  updateSearchNumber(value?: string): void {
    this.searchNumber$.next(value);
  }

  getFilteredColors = (color: string): Array<IColorModel> => {
    if (!color) return ColorService.allRows;
    const result: Array<string> = [];
    const initAllColors = [...ALL_COLORS];

    for (let i = 0; i < 3; i++) {
      try {
        const currentNearestColor = nearestColor.from(initAllColors)(color) as string;
        result.push(currentNearestColor);
        /**         */
        initAllColors.splice(initAllColors.indexOf(currentNearestColor), 1);
      } catch (e) {
        console.log('Oops');
      }
    }

    return ColorService.allRows.filter((row: IColorModel) => result.includes(row.color));
  };

  setSearchColor(color: string) {
    if (!color) {
      this.searchColor$.next('');
    } else {
      try {
        this.searchColor$.next(color);
      } catch (e) {
        console.log('Oops, something wrong');
      }
    }
  }

  constructor() {
    ColorService.allRows = ALL_ROWS.map((row, index) => ({ ...row, color: ALL_COLORS[index] }));
  }
}

export const colorService = new ColorService();
